/* 10. BMI */
.bmi-area {
    background: $dark;
    .bmi-text {
        padding-left: 30%;
    }
    .bmi-btn {
        width: 100%;
        overflow: hidden;
    }
}
.form-group {
    width: 30%;
    float: left;
    overflow: hidden;
    margin-right: 3%;
    label {
        font-weight: 500;
        text-transform: uppercase;
    }
    input {
        border: none;
        display: inline-block;
        padding: 13px 20px;
    }
}
