/* 5. Banner */
.banner-area {
    padding: 200px 0 180px;
    margin-top: -20px;
}
.banner-bg {
    background-color: $placeholder;
    background-image: url('../images/banner-bg.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    .banner-text {
        padding-left: 100px;
    }
    .template-btn {
        margin-top: 30px;
        &:hover {
            color: $light!important;
        }
    }
}